import { MiddlewareConsumer, Module } from '@nestjs/common';

import { AppScenario } from '../application/scenario/app.scenario';
import { AppController } from '../presentation/app.controller';
import { TrackerMiddleware } from '../presentation/middleware';

import { ProjectModule } from './project.module';
import { UtilityModule } from './utility.module';

@Module({
  imports: [UtilityModule, ProjectModule],
  controllers: [AppController],
  providers: [AppScenario],
})
export class AppModule {
  configure(consumer: MiddlewareConsumer) {
    consumer.apply(TrackerMiddleware).forRoutes('*');
  }
}
